* {
  margin: 0;
  padding: 0;
}

html{
  scroll-behavior: smooth;
}

body {
  font-family: 'Montserrat', sans-serif;
  background-color: #191818;
  color: white;
}

body {
   overflow-x: hidden; //For scrolling animation
}
nav {
  height: 10vh;
}
.no-scroll {
  overflow: hidden;  
}

.nav-links {
  display: flex;
  list-style: none;
  width: 30%;
  height: 100%;
  justify-content: space-around;
  align-items: center;
  margin-left: auto;
}
.nav-links li a {
  color: white;
  text-decoration: none;
  font-size: 16px;
}

.landing {
  height: 90vh;   // 90 + 10 from nav = 100 fullscreen 
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; // for flexbox position under
  justify-content: space-around; 
}
.scroll-text {
  font-size: 16pt;
  z-index: -1;
}
.scroll-down {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.workSection {
  height: 200pt;
  display: block;
  position: relative;
}

.landing h1 {
  font-family: "Secular One";
  font-size: 13vw;
  color: #FFF55A;
  z-index: -1;
}

.footer-class {
  height: 10vh;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #191818;
}
.modaal-container {
  top: 5vw;
  width: 100vw;
  max-width: 66vw;
}
.modaal-wrapper .modaal-close {
  top: 5em;
  right: 2em;
}
.modaal-heading {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  margin-bottom: 115px;
  font-size: 4vw;
}

.icon-ruby{
  position: absolute;
  -webkit-filter: drop-shadow(12px 12px 25px rgba(0,0,0,0.5));
  width: 20%;
  margin-top: -88pt;
  left: 10%;
  z-index: -1;
}

.icon-jekyll {
position: absolute;
margin-left: auto;
margin-right: auto;
left: 0;
right: 0;
width: 20%;
-webkit-filter: drop-shadow(12px 12px 25px rgba(0,0,0,0.5));
margin-top: 25pt;
}

.jekyllHead {
  font-weight: 700;
  font-size: 3em;
  line-height: 2;
  margin-top: -100pt;
}

.parJekyll {
  font-weight: 700;
  font-size: 1.5em;
  line-height: 50px;
}

.parCoolColor {
  color: #5ee5d5;
}

.flyingText {
  font-size: 80pt;
  position: absolute;
  right: -16%;
  margin-top: 30px;
  margin-left: -100px;
}

.box {
  position: absolute;
  width: 60pt;
  height: 60pt;
}

.linkBox{
  display: flex;
  justify-content: center;
  align-items: center;
}

.linkBox h3 {
  text-decoration: none;
  font-weight: 900;
  color: #191818;
}

.upperWork {
  display: flex;
  justify-content: center;
}

.wordpressText {
  font-weight: 700;
  font-size: 30pt;
  height: auto;
}

.lastSection {
  font-size: 20pt;
  line-height: 170pt;
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 85vh;
  justify-content: flex-end;
  text-align: center;
}

.bubbles {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 0;
  top: 100%;
}

.bubble {
  position: absolute;
  bottom: 0;
  width: 20px;
  height: 20px;
  background: #bf0671;
  border-radius: 50%;
  opacity: 0.8;
  animation: flying 1.5s infinite ease-in;
  left: 52%;
  margin-bottom: -6em;
}

.x1{
  animation-duration: 6s;
  animation-delay: 4s;
  animation: sideFlying 2s infinite ease-in-out;
}
.x2 {
  animation-duration: 2s;
  animation-delay: 3s;
}

@keyframes flying {
  0% {
    bottom: 30em;
    transform: translateX(0);
  }

  50% {
    transform: translateX(2vw);
    transform: translateY(-2vw);
  }

  100% {
    bottom: 700px;
    transform: translateX(-1vw);
    opacity: 0;
  }
}

@keyframes sideFlying {
  0% {
    bottom: 30em;
    transform: translateX(0);
  }

  50% {
    transform: translateX(-3vw);
    transform: translateY(-3vw);
  }

  100% {
    bottom: 700px;
    transform: translateX(40px);
    opacity: 0;
  }
}

/*--------------- @media queries ---------------*/

/*--------------- nav responsive ---------------*/
@media screen and (max-width: 768px) {

  .line{
    width: 30px;
    height: 3px;
    background: white;
    margin: 5px;
  }
  nav{
    position: relative;
  }
  .hamburger {
    position: absolute;
    cursor: pointer;
    right: 5%;
    top: 50%;
    transform: translate(-5%, -50%);
    z-index: 2;
  }
  .nav-links {
    position: fixed;
    background: #5b78c7;
    height: 100vh;
    width: 100%;
    flex-direction: column;
    clip-path: circle(100px at 90% -17%);
    -webkit-clip-path: circle(100px at 90% -17%);
    transition: all 1s ease-out;
    pointer-events: none;
  }
 .nav-links.open {
    clip-path: circle(1050px at 90% -10%);
    -webkit-clip-path: circle(1050px at 90% -10%);
    pointer-events: all;
  }
  .landing {
    flex-direction: column;
  }
  .nav-links li {
    opacity: 0;
  }
  .nav-links li a {
    font-size: 25px;
  }
  .nav-links li:nth-child(1) {
    transition: all 0.5s ease 0.2s;
  }
  .nav-links li:nth-child(2) {
    transition: all 0.5s ease 0.4s;
  }
  .nav-links li:nth-child(3) {
    transition: all 0.5s ease 0.6s;
  }
  li.fade{
    opacity: 1;
  }

  .scroll-dis {
    overflow: hidden;
  }

  .fitBox {
    margin-left: 50pt;
  }

}

@media (min-width: 768px) and (max-width: 1024px) {
  .bubbles {
    top: 58em;
  }
}
@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) { 
  .bubbles {
    top: 48em;
  }
}

@media (min-width: 481px) and (max-width: 767px) {
  .bubbles {
    top: 57em;
  }
  .icon-ruby {
    left: 3%;
  }
}

@media (min-width: 320px) and (max-width: 480px) {
  .bubbles {
    top: 60em;
  }
  .bubble {
    width: 15px;
    height: 15px;
  }
  .icon-ruby {
    left: 3%;
    bottom: -38em;
  }
}

